.CookieConsent {
  align-items: baseline;
  background: rgb(43, 55, 59);
  color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  left: 0;
  position: fixed;
  width: 100%;
  z-index: 999;
  bottom: 0;
  padding: 15px;
  box-sizing: border-box; /* เพิ่ม padding เข้าไปในขนาดทั้งหมดของ div */
}

body {
  background-color: #123;
  color: #fff;
  font-family: Arial, sans-serif;
}

main {
  width: 300px;
  margin: 50px auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

div.board {
  display: grid;
  height: 300px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

div.square {
  width: 100px;
  height: 100px;
  border: 2px solid #fff;
  box-sizing: border-box;
  font-size: 4rem;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

div.square:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

div.result {
  font-size: 2rem;
  padding: 10px;
}

div.result.green {
  background-color: #5a5;
  border-radius: 12px;
}

div.result.red {
  background-color: #a55;
  border-radius: 12px;
}

div.result.blue {
  background-color: #002BFF; /* เพิ่มเครื่องหมาย ; ต่อท้ายสีพื้นหลัง */
  border-radius: 12px;
}


.reset-button {
  margin-top: 20px;
  padding: 10px 20px;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #333;
  color: #fff;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.reset-button:hover {
  background-color: #555;
}

.CookieConsent .button-container {
  flex: 0 0 auto;
}

.CookieConsent button {
  background: rgb(255, 212, 45);
  border: 0;
  border-radius: 0;
  box-shadow: none;
  color: rgb(78, 80, 59);
  cursor: pointer;
  padding: 5px 10px;
  margin-left: 15px; /* เพิ่มขอบซ้ายของปุ่ม */
  font-size: 13px;
}

#rcc-confirm-button {
  background: rgb(255, 212, 45);
  border: 0;
  border-radius: 5px; /* ปรับเป็น 5px จาก 0px */
  box-shadow: none;
  color: rgb(78, 80, 59);
  cursor: pointer;
  flex: 0 0 auto;
  padding: 10px 20px; /* ปรับเป็น 10px ด้านบนและด้านล่าง, 20px ด้านซ้ายและด้านขวา */
  margin: 15px;
  font-size: 1rem; /* ปรับเป็น 1rem */
  transition: background-color 0.3s ease;
}

#rcc-confirm-button:hover {
  background-color: #ffc700; /* เมื่อ hover ให้เปลี่ยนสีเป็น #ffc700 */
}
